<script>
import { DxDataGrid, DxColumn } from "devextreme-vue/data-grid";
export default {
  name: "ProductDetails",
  props: ["isHide"],
  components: {
    DxDataGrid,
    DxColumn,
    // DxRadioGroup
  },
  data() {
    return {};
