<script>
import * as axios from "axios";
import Details from "./ProductListDetails.vue";
export default {
  name: "Product",
  props: {},
  components: {
    Details,
    // MsButton
  },
  methods: {
    onSelectionChanged({ products }) {
      const data = products[1];

      this.showEmployeeInfo = !!data;
      this.selectedRowNotes = data && data.Notes;
      this.selectedRowPicture = data && data.Picture;
    },
    btnAddOnClick() {
      this.isHideParent = !this.isHideParent;
    },
    outIsHide(e) {
      this.isHideParent = e;
    },
  },
  data() {
    return {
      typePage: 25,
      currentPage: 1,
      isHideParent: true,
      allMode: "allPages",
      checkBoxesMode: "always",
      products: [
        {
          productID: "00000000-0000-0000-0000-000000000000",
          productName: "Res01",
          sku: "Res 01",
          status: 0,
          categoryCode: 0,
          categoryName: "",
          buyPrice: 0,
          sellPrice: 0,
          unitCode: 1,
          unitName: "Đôi",
          isShow: 1,
          description: "",
        },
      ],
    };
  },
  async created() {
    // const response = await axios.get(
    //   'http://localhost:55810/api/Products/Paginate?offset=1&limit=25&sellPrice=0&isShow=2&status=2'
    // );
    // this.products = response.data;
    axios
      .get('http://localhost:55810/api/Products/Paginate?offset=1&limit=25&sellPrice=0&isShow=2&status=2')
      .then(response => (this.products = response.data))
  // }
    // console.log(this.products)
  },
};
</script>
